<template>
  <div class="container pt-5 pb-5" style="max-width: 500px; text-align: left">
    <div class="alert alert-success" role="alert">
      <h2 class="alert-heading text-center">Liên hệ với chúng tôi</h2>
    </div>

    <form @submit.prevent="handleSubmit" method="POST">

      <div class="form-group mt-4">
        <label class="mb-2" for="email">Email:</label>
        <input
          type="email"
          v-model="userForm.email"
          id="email"
          name="email"
          class="form-control"
          :class="{ 'is-invalid': isSubmitted && $v.userForm.email.$error }"
        />
        <div
          v-if="isSubmitted && $v.userForm.email.$error"
          class="invalid-feedback"
        >
          <span v-if="!$v.userForm.email.required"
            >Email không được bỏ trống</span
          >
          <span v-if="!$v.userForm.email.email"
            >Vui lòng nhập đúng định dạng Email</span
          >
        </div>
      </div>


       <div class="form-group mt-4">
        <label class="mb-2" for="message">Tin nhắn:</label>
        <textarea
          type="text"
          v-model="userForm.message"
          id="message"
          name="message"
          class="form-control"
          :class="{ 'is-invalid': isSubmitted && $v.userForm.message.$error }"
        />
        <div
          v-if="isSubmitted && !$v.userForm.message.required"
          class="invalid-feedback"
        >
          Nội dung tin nhắn không được bỏ trống
        </div>
      </div>
      <div class="form-group">
        <button  class="btn btn-info w-100 mt-4 btn-block">Gửi tin nhắn</button>
      </div>
    </form>
  </div>
</template>
<script>
import { required, email, } from "vuelidate/lib/validators";
import { mapGetters, mapActions, mapMutations } from "vuex";
export default {
  name: "Register",
  data() {
    return {
      userForm: {
        email: "",
        address:"",
        message:""
      },
      isSubmitted: false,
    };
  },
  validations: {
    userForm: {
      email: {
        required,
        email,
      },
      message: {
        required,
      }
    },
  },
  created:{
  },
  mounted(){
  },
  computed:{
      ...mapGetters['']
  },
  methods: {
    handleSubmit() {
      this.isSubmitted = true;
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }else{
        alert("Bạn đã gửi tin nhắn thành công !!!")
        
      }
    //   alert("SUCCESS!" + JSON.stringify(this.userForm));
    },
    ...mapActions['registerAccount'],
    ...mapMutations['registerAccount']
  },
};
</script>

<style>
.form-group > label {
  font-weight: 600;
}
</style>